<template>
  <div>
    <!-- 1.user.vue -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 2. -->
    <el-card>
      <!-- 3.Alter -->
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
      <!-- 4.Steps, title, :  -->
      <!-- :active, 5. jsactiveIndex -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- 8., formtabs, : tabstabs-pane -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="ruleFormRef"
        label-width="100px"
        label-position="top"
      >
        <!-- label-position="top" : label -->
        <!-- 6.tabs -->
        <el-tabs
          :tab-position="'left'"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <!-- 7.label, : , name, v-model,activeIndex,, activenumber,  activeIndex - 0 -->
          <el-tab-pane label="" name="0">
            <!-- 10. ,prop,  -->
            <!-- 11. -->
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <!-- type="number" : -->
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <!-- 12., v-model="addForm.goods_cat", dataaddForm -->
              <el-cascader
                expand-trigger="hover"
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- 18.item -->
            <el-form-item v-for="item in manyTableData" :label="item.attr_name" :key="item.attr_id">
              <!-- 20.checkBox, margin -->
              <!-- v-model="item.attr_vals" -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox v-for="(item2, i) in item.attr_vals" :label="item2" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <!-- 22. -->
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- 23.Upload -->
            <!-- action:api,data, ,  24, 25, list-type="picture" -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
            >
              <!-- 26., network, networktoken, :axios, token, headers -->
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!-- 38.,vue-quill-editor, github, main.js, ,  .ql-editor { min-height: 300px;} -->
            <quill-editor  v-model="addForm.goods_introduce" />
            <!-- v-model, , data -->
            <!-- 39. -->
            <el-button type="primary" style="margin-top:15px;" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- 35.Dialog, previewVisible : false -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <!-- 36.img -->
      <img :src="previewPath" alt style="width:100%;" />
      <!-- 37. this.previewVisible = true, -->
    </el-dialog>
  </div>
</template>

<script>
////lodash_
import _ from 'lodash'
export default {
  data() {
    return {
      //5.
      activeIndex: '0',
      //9.
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        //
        goods_cat: [],
        //
        pics: [],
        //
        goods_introduce:'',
        //
        attrs:[]

      },
      //
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      //
      catelist: [],
      //
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      //
      manyTableData: [],
      //
      onlyTableData: [],
      //url
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',//27.
      //headers
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      //
      previewPath: '',
      previewVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //11.1.6.1, 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //data
      this.catelist = res.data
      //   console.log(this.catelist)
    },
    //13.
    handleChange() {
      console.log(this.addForm.goods_cat)
      //14.
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
        return
      }
    },
    //14. , , tab:beforeTabLeave :before-leave="beforeTabLeave"
    beforeTabLeave(activeName, oldActiveName) {
      // console.log(":"+ oldActiveName);
      // console.log(":"+ activeName);
      // return false; //: 
      //, 
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    //15.tab@tab-click="tabClicked", tab, 
    async tabClicked() {
      // console.log(this.activeIndex);//name
      if (this.activeIndex === '1') {
        //16. : 1.7.1, 
        //id
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200)
          return this.$message.error('')
        //, data
        //   this.manyTableData = res.data;
        //19. item,, attr_vals
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
        })
        this.manyTableData = res.data

        //18.
      } else if (this.activeIndex === '2') {
        //21.. , , paramsonly
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200)
          return this.$message.error('')
        //data
        this.onlyTableData = res.data
        //22.
      }
    },
    //24.
    handlePreview(file) {
      //34., , data, 
      this.previewPath = file.response.data.url
      //35.Dialog
      //37.
      this.previewVisible = true;
      //38.,
    },
    //25.
    handleRemove(file) {
      //31. , , 
      //console.log(file); //
      const filePath = file.response.data.tmp_path
      //32.pics
      const i = this.addForm.pics.findIndex(item => item.pic === filePath)
      //33. splice
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    //27., , :on-success="handleSuccess"
    handleSuccess(res) {
      // console.log(res);
      //28. dataaddFormpics:[]
      //29. : 
      const picInfo = { pic: res.data.tmp_path }
      //30.addForm.pics, 
      this.addForm.pics.push(picInfo)
    },
    //40. 
    add(){
        // console.log(this.addForm);//
        this.$refs.ruleFormRef.validate(async valid =>{
            if(!valid) return this.$message.error("");
            //
            //41. , , 
            // this.addForm.goods_cat = this.addForm.goods_cat.join(',');
            //42., v-model, , lodash, cloneDeep(), lodash, 
            const form = _.cloneDeep(this.addForm);
            form.goods_cat = form.goods_cat.join(',');
            //
            // console.log(form);
            //43. , , dataaddFormattrs
            this.manyTableData.forEach(item =>{
                //, push
                const newInfo = {
                    attr_id: item.attr_id,
                    attr_value: item.attr_vals.join(',')
                }
                this.addForm.attrs.push(newInfo);
            })

            //44. 
            this.onlyTableData.forEach(item =>{
                //, push
                const newInfo = {
                    attr_id: item.attr_id,
                    attr_value: item.attr_vals
                    //value, 
                }
                this.addForm.attrs.push(newInfo);
            });
            //45. form
            form.attrs = this.addForm.attrs;
            //46.,  1.8.2
            const {data: res} = await this.$http.post('goods', form);
            if(res.meta.status !== 201) return this.$message.error("");
            //
            this.$message.success("");
            //
            this.$router.push('/goods');


        })
    },
  },
  computed: {
    //17.id
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
//, 
.el-steps {
  margin: 15px 0;
}

.el-checkbox {
  margin: 0 10px 0 0 !important;
}

// 
.el-step__title {
  font-size: 13px;
}
</style>