<template>
  <div>
    <!-- 1.user.vue -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 2. -->
    <el-card>
      <!-- 3.button -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>

      <!-- 8.vue-table-width-tree-grid, github, mian.js -->
      <!-- 9. githubexample, , columns
:data() :columns() :selection-type()
:expand-type() show-index() index-text()
      border() :show-row-hover(), -->
      <tree-table
        class="treeTable"
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
        <!-- 11. , slot="isok" -->
        <template slot="isok" slot-scope="scope">
          <!-- 12. , cat_deleted, if -->
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen;"
          ></i>
          <i class="el-icon-error" v-else style="color: red;"></i>
        </template>

        <!-- 14.  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
          <el-tag size="mini" type="success" v-else-if="scope.row.cat_level===1"></el-tag>
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>

        <!-- 16.  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit"></el-button>
          <el-button type="danger" icon="el-icon-delete"></el-button>
        </template>
      </tree-table>

      <!-- 17. -- -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!-- 20.,  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClosed"
    >
      <!-- 23., 4 -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <!-- 28., item -->
        <el-form-item label=":">
          <!-- 31. Cascader, hover -->
          <!--expand-trigger="hover" 
          options props, data
          v-model,, :, id 
          clearable(X) change-on-select()-->
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //5.
      cateList: [],
      //6. 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      //7.
      total: 0,
      // table  label, prop
      columns: [
        { label: '', prop: 'cat_name' }, //
        //10. , githubcolumns
        {
          label: '',
          //
          type: 'template',
          //
          template: 'isok'
        },
        //13. 
        {
          label: '',
          type: 'template',
          template: 'order'
        }, //15. 
        {
          label: '',
          type: 'template',
          template: 'opt'
        }
      ],
      //21. 
      addCateDialogVisible: false,
      //24. 
      addCateForm: {
        //25. 
        cat_name: '',
        //27. , id
        cat_pid: 0,
        //, 1
        cat_level: 0
      },
      //26. 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //30. 
      parentCateList: [],
      //32. 
      cascaderProps: {
        value: 'cat_id', //id
        label: 'cat_name', //
        children: 'children', //
        expandTrigger: 'hover' //
      },
      //33.id
      selectedKeys: []
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //4. 1.6.1, , data
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      //, data
      this.cateList = res.data.result
      //7. 
      this.total = res.data.total
    },
    //18.pagesize
    handleSizeChange(newsize) {
      //queryInfo
      this.queryInfo.pagesize = newsize
      this.getCateList() //
    },
    //19.pagenum
    handleCurrentChange(newnum) {
      this.queryInfo.pagenum = newnum
      this.getCateList() //
    },
    //22. 
    showAddCateDialog() {
      //30. 
      this.getParentCateList()
      this.addCateDialogVisible = true //
    },
    //29. 1.6.1, , , 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      //data
      this.parentCateList = res.data
      //
    },
    //34. 
    parentCateChanged() {
      console.log(this.selectedKeys) //id
      //35., id
      if (this.selectedKeys.length > 0) {
        //
        //id == 
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ]
        //36. 
        this.addCateForm.cat_level = this.selectedKeys.length
        return;
      } else {
        //, 0
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    //35. , 
    addCate() {
      console.log(this.addCateForm); //
      //37., 1.6.2
      this.$refs.addCateFormRef.validate(async valid =>{
          if(!valid) return;
          const {data: res} = await this.$http.post('categories',this.addCateForm);
          if(res.meta.status !== 201) return this.$message.error("");
          //,
          this.$message.success("");
          this.getCateList();
           this.addCateDialogVisible = false //

      })

    },
    //36., dialog@close, 
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      //
      this.selectedKeys = [];
      //
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}

.el-cascader {
  width: 100%;
}
</style>