<template>
  <div>
    <!-- 1.user.vue -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 2. -->
    <el-card>
      <!-- 3.alert, :closable="false"X show-icon -->

      <el-alert title=":!" type="warning" :closable="false" show-icon></el-alert>

      <!-- 4. -->
      <el-row class="cat_opt">
        <el-col>
          <span>:</span>
          <!-- , ,Cascader -->
          <el-cascader
            expand-trigger="hover"
            v-model="selectedCateKeys"
            :options="catelist"
            :props="cateProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- 11.tabs, v-model="activeName" ,data-->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <!-- 14., 3,, computed  -->
          <el-button
            @click="addDialogVisible = true"
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
          ></el-button>

          <!-- 21. -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <!-- 41. , closable -->
              <!-- 42. , ,, forEach -->
              <template slot-scope="scope">
                <el-tag closable v-for="(item,i) in scope.row.attr_vals" :key="i" @close="handleClose(i,scope.row)">{{ item }}</el-tag>
                <!-- 43., ,datav-ifinputVisible, v-modelinputValue, scope.row -->
                <!-- 48.taginput, scope.row  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                <!-- 49.scope.row -->
                <!-- 49.row, row.inputValue = true;-->
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                  type="primary"
                  icon="el-icon-edit"
                ></el-button>
                <el-button
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                  type="danger"
                  icon="el-icon-delete"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            @click="addDialogVisible = true"
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
          ></el-button>
          <!-- 22.---. 23.-->
          <el-table :data="onlyTableData" border stripe>
            <!-- 58. ,  -->
            <el-table-column type="expand">
              <!-- 41. , closable -->
              <!-- 42. , ,, forEach -->
              <template slot-scope="scope">
                <el-tag closable v-for="(item,i) in scope.row.attr_vals" :key="i" @close="handleClose(i,scope.row)">{{ item }}</el-tag>
                <!-- 43., ,datav-ifinputVisible, v-modelinputValue, scope.row -->
                <!-- 48.taginput, scope.row  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                <!-- 49.scope.row -->
                <!-- 49.row, row.inputValue = true;-->
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <!-- 31. -->
                <el-button
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                  type="primary"
                  icon="el-icon-edit"
                ></el-button>
                <el-button
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                  type="danger"
                  icon="el-icon-delete"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 23., , ,  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 27. -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <!-- prop -->
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>

    <!-- 32., , add, ctrl+d, edit -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      catelist: [],
      //7.
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //8. 
      selectedCateKeys: [],
      //12.,   //13.tab
      //   activeName:'first',
      activeName: 'many',
      //
      manyTableData: [],
      onlyTableData: [], // 
      //25. 
      addDialogVisible: false,
      //26. , @click="addDialogVisible = true"
      //27.  , 
      addForm: {
        attr_name: ''
      },
      //28. 
      addFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //29. , @close
      //33. 
      editDialogVisible: false,
      //34. @close
      //35. 
      editForm: {},
      //36. 
      editFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      } ,//37. 
      //44. 
      inputVisible: false,
      //
      inputValue :'',
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //6. 1.6.1 3, 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200)
        return this.$message.error('')
      //data
      this.catelist = res.data
    },
    //9.
    async handleChange() {
      console.log(this.selectedCateKeys) //
      //10. ,3
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = [] //;
        //57.,2,
        this.manyTableData = [];
        this.onlyTableData = [];
        return; //11.tabs
      }
      //18. id1.7.1
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200)
        return this.$message.error('')
      //, //19. , 

      //40. , , , 
      //   console.log(res.data);
      //41. 
      res.data.forEach(item => {
        //   item.attr_vals = item.attr_vals.split(",")
        // 42. , ,, forEach
        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : [];

        //47. :item
        item.inputVisible = false;
        item.inputValue = '';
        //48. v-if="scope.row.inputVisible" v-value
      })
      console.log(res.data) //41.

      //20.table, activeName
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    //13.tab
    handleClick() {
      // console.log(this.activeName);
      this.handleChange() //19. , 
    },
    //29. , 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    //30.1.7.2
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) return this.$message.error('')
        //
        this.$message.success('')
        this.handleChange()
        this.addDialogVisible = false
      })
    },
    //31.click, 32.
    async showEditDialog(attr_id) {
      //38. , id, scope.row.attr_id, 1.7.4
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        {
          params: {
            attr_sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      //
      this.editForm = res.data

      this.editDialogVisible = true //
    },
    //34.
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //37. 
    editParams() {
      //39.1.7.5
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 200) return this.$message.error('')
        //
        this.$message.success('')
        this.handleChange()
        this.editDialogVisible = false; //
      })
    },
    //38.id1.7.3
    async removeParams(attr_id) {
      //39.
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      //
      if (confirmResult !== 'confirm') {
        return this.$message.info('!')
      }
      //1.7.3
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attr_id}`
      )
      if (res.meta.status !== 200) return this.$message.error('')
      //
      this.$message.success('')
      this.handleChange()
      //40. , , , handleChange
    },
    //45.enter
    async handleInputConfirm(row){
        //52. , , , , ,
        if(row.inputValue.trim().length === 0){
            row.inputValue = '';
            row.inputVisible = false;
            return;
        }
        //53. 
        row.attr_vals.push(row.inputValue.trim());
        //
        row.inputValue = '';
        row.inputVisible = false;
        //54. , , 1.7.5
        const {data: res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
            attr_name: row.attr_name,
            attr_sel: row.attr_sel,
            attr_vals: row.attr_vals.join(',') //:vals, 
        })
        if(res.meta.status !== 200) return this.$message.error("");
        //
        this.$message.success("");


    },
    //46., ,50. (: )
    showInput(row){
        //this.inputVisible = true;// 
        //, taginput,forEach
        row.inputVisible = true;//50. 
        //51.  $nextTick(): 
         this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });

    },
    //55. el-tag@close="handleClose(i)"
    async handleClose(i, row){
        //
        row.attr_vals.splice(i,1);
        //, , 
        this.saveAttrVals(row);

    },
    //56.
    async saveAttrVals(row){
        const {data: res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
            attr_name: row.attr_name,
            attr_sel: row.attr_sel,
            attr_vals: row.attr_vals.join(',')
        })
        if(res.meta.status !== 200) return this.$message.error("");
        //
        this.$message.success("");

    }



  },
  //15.
  computed: {
    //15. , true, false
    isBtnDisabled() {
      return this.selectedCateKeys.length !== 3 ? true : false;
    },
    //16.1.7.1, computed , id
    //17. , manyonly,namedataactiveName
    cateId() {
      if (this.selectedCateKeys.length === 3) return this.selectedCateKeys[2]
      return null
    },
    //24. 
    titleText() {
      return this.activeName === 'many' ? '' : ''
    }
  }
}
</script>


<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 10px;
}
.input-new-tag{
    width: 150px;
}
</style>