<template>
  <div>
    <!-- 1.user.vue -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 2. -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <!-- 3.input, v-model  -->
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
              <!-- 9.,v-model, , clearable,@clear -->
            <el-button slot="append" @click="getGoodsList" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>
      <!-- 4. ,  -->
      <!-- 5.table -->
      <el-table :data="goodsList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column width="95" label="()" prop="goods_price"></el-table-column>
        <el-table-column width="70" label="" prop="goods_weight"></el-table-column>
        <el-table-column width="140" label="" prop="add_time">
          <template slot-scope="scope">
            <!-- 6.main.js -->
            {{ scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column width="130" label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
            <!-- 10. -->
            <el-button size="mini" @click="removeById(scope.row.goods_id)" type="danger" icon="el-icon-delete"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 7., ,  background: -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 15, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //5.
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      goodsList: [], //
      total: 0 //
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    //4. 1.8.1
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      //data
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    //8.handleSize,size
    // 9.
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
      this.getGoodsList()
    },
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage
      this.getGoodsList()
    },
    //10.id
    async removeById(id){
        //11.
         const confirmResult =await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err);
        if(confirmResult !== 'confirm'){
            return this.$message.info("")
        }
        //, 1.8.5
        const {data: res} = await this.$http.delete(`goods/${id}`)
        if(res.meta.status !== 200) return this.$message.error("");
        //
        this.$message.success("");
        this.getGoodsList();
    },
    //12.
    goAddpage(){
        this.$router.push('/goods/add')
    },//13., 
  }
}
</script>

<style lang="less" scoped>
</style>